<template>
    <div class="program">
        <hheader :header="hheader">
            <i class="iconfont icon-ren" slot="hleft"></i>
        </hheader>
        <section class="container has-header has-gray has-footer">
            <!-- 内容 -->
            <section class="main">
                <list></list>
            </section>
        </section>
        <hfooter></hfooter>
    </div>
</template>

<script>
import list from "../components/list.vue";
export default {
    data() {
        return {
            hheader: {
                title: "基坑监测项目",
                back: "true1"
            }
        };
    },
    components: {
        list
    }
};
</script>

<style lang="scss" rel="stylesheet/scss">
@import "../assets/hcsy.scss";
.program {
    .main {
        margin: rem(20);
    }
}
.item-pr {
    border-radius: rem(10);
    background-color: white;
    font-size: rem(40);
    h4 {
        height: rem(95);
        line-height: rem(95);
        padding: 0 rem(30);
        > span {
            background-color: #cecece;
            padding: rem(1) rem(10);
            color: white;
            height: rem(40);
        }
        strong {
            font-weight: 300;
        }
    }
    ul {
        li {
            padding: 0 rem(30);
            display: block;
            border-top: 1px solid $bordercolor;
            height: rem(95);
            line-height: rem(95);
            text-indent: rem(30);
            margin-bottom: rem(35);
            span {
                color: #8d8d8d;
            }
            b {
                float: right;
                color: $color;
            }
        }
        li::before {
            content: "";
            display: block;
            width: rem(10);
            height: rem(10);
            border-radius: 50%;
            background-color: #8d8d8d;
            line-height: rem(95);
            float: left;
            margin-top: rem(42);
        }
    }
}
</style>



